/**
 * @module gmfapp.share
 */
const exports = {};

import './share.css';
import './common_dependencies.js';
import gmfBase from 'gmf/index.js';

/** @suppress {extraRequire} */
import gmfPermalinkShareComponent from 'gmf/permalink/shareComponent.js';

/** @suppress {extraRequire} */
import ngeoMessageModalComponent from 'ngeo/message/modalComponent.js';


/** @type {!angular.Module} **/
exports.module = angular.module('gmfapp', [
  gmfBase.module.name,
  ngeoMessageModalComponent.name,
  gmfPermalinkShareComponent.name,
]);

exports.constant('angularLocaleScript', '../build/angular-locale_{{locale}}.js');
exports.constant('gmfShortenerCreateUrl', 'https://geomapfish-demo.camptocamp.net/2.2/wsgi/short/create');


/**
 * @constructor
 * @ngInject
 */
exports.MainController = function() {

  /**
   * Model attached to the modal to toggle it
   * @type {boolean}
   * @export
   */
  this.modalShareWithEmailShown = false;

  /**
   * Model attached to the modal to toggle it
   * @type {boolean}
   * @export
   */
  this.modalShareShown = false;

};


exports.module.controller('MainController', exports.MainController);


export default exports;



